import { isObject } from '@/utils/util'

const state = {
  customer: {}, // 
  balances: {}, // 
  gameStatus: {}, // 
  showSideBar: false, // 
  showLoginDialog: false, // 
  token: null, // base64token
  saveDewFigureKey: ['D051', 'T032', 'B001'], // 
  socketResultList: {} // 
}
let localState = window.localStorage.getItem('redux_state')
localState = (localState && JSON.parse(localState))
localState = isObject(localState) ? localState : {}
if (Date.now() < localState._cacheTime) {
  delete localState._cacheTime
  Object.assign(state, localState)
} else {
  window.localStorage.removeItem('redux_state')
}
export default state
